:root {
  --gray: rgb(149, 151, 153);
  --peach: rgb(248, 160, 120);
}

.placeholder {
  height: 120px;
}

.header {
  display: flex;
  height: 120px;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 2;
  background: white;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

.header.active {
  background: rgb(248, 160, 120);
  background: var(--peach);
}

.link {
  color: black;
  margin-right: 20px;
}

.link:hover {
  color: rgb(248, 160, 120);
  color: var(--peach);
}

.header.active .subtitle,
.header.active .social {
  color: white;
}

.header-item {
  align-items: center;
  display: flex;
  flex: 1 1;
}

.navigation {
  margin-left: 40px;
}

.navigation-item {
  color: rgb(149, 151, 153);
  color: var(--gray);
  margin: 0 10px;
}

.navigation-item:hover {
  color: rgb(248, 160, 120);
  color: var(--peach);
}

.title-container {
  display: flex;
  justify-content: center
}

.header-title {
  width: 300px;
}

.title {
  color: rgb(33, 121, 169);
  display: flex;
  font-size: 50px;
  justify-content: center;
}

.subtitle {
  color: rgb(149, 151, 153);
  color: var(--gray);
  display: flex;
  justify-content: center;
}

.social {
  justify-content: flex-end;
  margin-right: 40px;
}

.social-item {
  font-size: 20px;
  margin: 0 10px;
}

.social-item:hover {
  color: rgb(248, 160, 120);
  color: var(--peach);
}
.profile {
  width: 100%;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(6, 1fr);

  /* To by dynamically set*/
  grid-auto-rows: 90px;
}

.card {
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid;
  grid-column-end: span 2;
  grid-row-end: span 2;
}

.card img {
  display: flex;
  height: 100%;
  width: 100%;
  object-fit: cover;
}


.thumbnail {
  /*background: red;*/
  display:flex;
  align-items:center;
  justify-content:center;
}

.thumbnail img {
  max-width: 100%;
  max-height: 100%;
}

.thumbnail-selected {
  border: 3px solid;
  box-sizing: border-box;
}
